	"Deltamethrin is a pyrethroid ester. It is a highly effective insecticide, and it has been widely used by the agrochemical industry. Trace amounts of deltamethrin can be found in olive oil. Being a neurotoxin, and potentially an endocrine disruptor, its extraction from olive oil is therefore important. One of the possible techniques to achieve this is to use Molecularly Imprinted Polymers (MIP). A MIP is generated by creating a polymer matrix between some functional monomers - here Metacrylic Acid (MAA) and Acrylamide (AM) - and a template (here deltamethrin). After generation of the MIP, the template is removed, but affinity of the MIP towards the template is retained, allowing its extraction from other mediums (such as olive oil). In the paper below, quantum calculations showed that the complex formed (through Van-der-Walls interactions) between deltamethrin and AM (bottom picture) is more stable than the one formed with MMA (top picture). This rationalizes the better imprinting effect of AM towards deltamethrin.",	
